import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';

import { HttpClient, HttpHeaders, HttpParams  } from '@angular/common/http';

import { environment } from 'src/environments/environment';

const httpOptions = {
  headers: new HttpHeaders({
    'Content-Type' : 'application/json'
  })
};


@Injectable({
  providedIn: 'root'
})
export class ApiService {

  apiUrl = environment.rootUrl;

  constructor(
    private http: HttpClient
  ) { }

  addUpdateProgram(data): Observable<void> {
    return this.http.post<void>(`/api/Program/AddUpdateProgram`, data || {});
  }

  addUpdateClassLevel(data): Observable<void> {
    return this.http.post<void>(`/api/Class/AddUpdateClassLevel`, data || {});
  }

  addUpdateBatchMode(data): Observable<void> {
    return this.http.post<void>(`/api/Batch/AddUpdateBatchMode`, data || {});
  }

  addUpdateBatchCategory(data): Observable<void> {
    return this.http.post<void>(`/api/Batch/AddUpdateBatchCategory`, data || {});
  }

  addUpdateStyle(data): Observable<void> {
    return this.http.post<void>(`/api/Style/AddUpdateStyle`, data || {});
  }

  addUpdateRoom(data): Observable<void> {
    return this.http.post<void>(`/api/Room/AddUpdateRoom`, data || {});
  }

  addAppointmentCategory(data): Observable<void> {
    return this.http.post<void>(`/api/AppointmentCategory/AddAppointmentCategory`, data || {});
  }

}
